<template>
    <el-row>
        <el-col :span="6">*</el-col>
        <el-col :span="18">
            <el-upload
                class="syllabus-handout-upload"
                :headers="apiHeader"
                :file-list="fileList"
                drag
                :on-success="uploadSuccess"
                :on-change="handleChange"
                :on-remove="handleRemove"
                :on-progress="handleProgress"
                :on-error="handleAvatarError"
                :before-upload="beforeAvatarUpload"
                ref="onUpload"
                :action="materialUpload"
                v-loading="loading"
                element-loading-text="..."
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.6)"
            >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">
                    <em></em>
                </div>
                <div class="el-upload__tip" slot="tip">.pdf</div>
            </el-upload>
        </el-col>
    </el-row>
</template>
<style lang="less">
.syllabus-handout-upload {
    .el-upload__tip {
        line-height: 18px;
    }
}

</style>
<script>
import { getBaseUrl } from "../../util/config";
import { getCookie, setCookie } from "../../util/cookie.js";
import { SAAS_TOKEN } from "../../util/keys.js";
export default {
    computed: {
        materialUpload() {
            return `${getBaseUrl()}apigateway.gaodun.com/resource-api/resource/file`;
        }
    },
    data() {
        return {
            fileList: [],
            apiHeader: {},
            isUpload: false,
            loading: false,
        };
    },
    methods: {
        // 
        clearAllFileList(){
            this.fileList = [];
        },
        //
        beforeAvatarUpload(file) {
            var testmsg = file.name.substring(file.name.lastIndexOf(".") + 1);
            const extension = testmsg === "pdf";
            if (!extension) {
                this.$message({
                    message: " pdf!",
                    type: "warning"
                });
            }
            return extension;
        },
        //
        handleChange(file, fileList) {
            this.fileList = fileList.slice(-1);
        },
        //
        handleRemove(file, fileList) {
            if (fileList.length == 0) {
                this.isUpload = false;
                this.$emit('getHandoutPath','');
            }
            this.fileList = fileList;
        },
        //
        handleAvatarError(err, file, fileList) {
            this.loading = false;
            this.$message.error("");
            setTimeout(() => {
                this.fileList = [];
            }, 0);
        },
        // 
        handleProgress() {
            this.loading = true;
        },
        // 
        uploadSuccess(response, file) {
            this.loading = false;
            if (response.status === 0) {
                this.isUpload = true;
                this.$message({
                    message: "",
                    type: "success"
                });
                this.$emit('getHandoutPath',response.result.path);
            } else {
                this.isUpload = false;
                setTimeout(() => {
                    this.fileList = [];
                }, 0);
                this.$emit('getHandoutPath','');
                this.$message({
                    message: "",
                    type: "warning"
                });
            }
        },
    },
    mounted() {},
    async created() {
        let token = "Basic " + getCookie(SAAS_TOKEN);
        this.apiHeader = { Authentication: token };
    }
};
</script>



